<div class="time-container bg">
	<label>Date:</label>
	<div class="slider-container">
		<div id="slider-step"></div>
	</div>
	<label>Time:</label>
	<form>
		<div class="flex radio-group">
			<input on:click={update0} type="radio" id="option-one" name="selector">
			<label for="option-one">02–10</label>
			<input on:click={update1} type="radio" id="option-two" name="selector" checked>
			<label for="option-two">10–18</label>
			<input on:click={update2} type="radio" id="option-three" name="selector">
			<label for="option-three">18–02</label>
		</div>
	</form>
</div>

<div class="map-container" bind:this={container}>
	<div id="tooltip"></div>
</div>	

<Infobox/>

<div on:click={updateColorblind} class="colorblindicon">
	<img src="./colorwheel.png" width="30px" height="30px" alt="Toggle colorschemes (for colorblind)">
</div>


